




import { Component, Prop, Vue } from 'vue-property-decorator'

@Component({
  name: 'x-link.vue'
})
export default class XLink extends Vue {
  @Prop({ type: String })
  to?: string

  @Prop({ type: Boolean, default: false })
  external!: boolean

  get target() {
    return this.external ? '_blank' : '_self'
  }
}
